  "validated-model-form.controller.ts": "    @action\n    async onSave() {\n        if (this.existingNode !== undefined) {\n            this.toast.success('Saved!');\n        } else {\n            this.toast.error('Nothing to save');\n        }\n    }\n\n    @action\n    onWillDestroy() {\n        if (this.get('isDirty')) {\n            // Do something cool here\n        }\n    }\n\n    // Since there are two forms on the page, we need to report on both\n    @action\n    changeDirtEditForm(dirt: boolean) {\n        this.set('editDirt', dirt);\n    }\n    @action\n    changeDirtCreateForm(dirt: boolean) {\n        this.set('createDirt', dirt);\n    }\n\n    @computed('createDirt', 'editDirt')\n    get isDirty() {\n        return this.createDirt || this.editDirt;\n    }",
  "validated-model-form.demo-create.hbs": "        <ValidatedModelForm\n            @onSave={{action this.onSave}}\n            @modelName='node'\n            @onDirtChange={{this.changeDirtCreateForm}}\n            as |form|\n        >\n            <form.text\n                @valuePath='title'\n                @label='Title'\n            />\n            <form.textarea\n                @valuePath='description'\n                @label='Description'\n            />\n            <BsButton\n                @buttonType='submit'\n                @disabled={{form.disabled}}\n            >\n                Save\n            </BsButton>\n        </ValidatedModelForm>",
  "validated-model-form.demo-edit.hbs": "            <ValidatedModelForm\n                @onSave={{action this.onSave}}\n                @onWillDestroy={{action this.onWillDestroy}}\n                @model={{this.node}}\n                @disabled={{this.model.taskInstance.isRunning}}\n                @onDirtChange={{this.changeDirtEditForm}}\n                as |form|\n            >\n                <form.text\n                    @valuePath='title'\n                    @label='Title'\n                />\n                <form.textarea\n                    @valuePath='description'\n                    @label='Description'\n                />\n                <BsButton\n                    @onClick={{action form.rollback}}\n                    @disabled={{not form.isDirty}}\n                >\n                    Cancel\n                </BsButton>\n                <BsButton\n                    @buttonType='submit'\n                    @disabled={{form.disabled}}\n                >\n                    Save\n                </BsButton>\n            </ValidatedModelForm>",
  "validated-model-form.route.ts": "    // This tells ember-onbeforeupload's ConfirmationMixin whether or not to stop transitions\n    @computed('controller.isDirty')\n    get isPageDirty() {\n        const controller = this.controller as ValidatedModelFormController;\n        return () => controller.get('isDirty');\n    }"
